<template>
    <b-container class="neworders" fluid>
        <b-row>
            <b-col><h1 class="mb-4">New Orders</h1></b-col>
            <b-col><DateGlobal  @send-date="getTypeData" @send-month="getMonth" @send-day="getDay" @send-year="getYear" /></b-col>
        </b-row>
        <CardNeworders :date="date" />
        <TableNeworders :date="date" />
    </b-container>
</template>

<script>
import DateGlobal from "../components/DateGlobal.vue"
import CardNeworders from '@/views/NewOrders/CardNeworders.vue'
import TableNeworders from '@/views/NewOrders/TabelNeworders.vue'

export default{
    name:'NewOrders',
    components:{
        DateGlobal,
        CardNeworders,
        TableNeworders
        
    },

    data(){
        return{
            date:'',
        }
    },

    methods:{
        getTypeData(payload){
            this.date=payload
        },
        getMonth(payload){
            this.date=payload
        },
        getDay(payload){
            this.date=payload
            
        },
        getYear(payload){
            this.date=payload
            
        },
    }
}
</script>