<template>
    <section class="allproduts">
        <b-container>
            <b-row>
                <b-col cols="12">
                    <h1 class="text-center fw-bolder mb-sm-4">All categories</h1>
                </b-col>
                <!----- search category ----->
                <b-col sm="8">
                    <b-form-group label="Filter" label-for="filter-input" label-cols-sm="3" label-align-sm="right"
                        label-size="sm" class="mb-0">
                        <b-input-group size="sm">
                            <b-form-input id="filter-input" v-model="filter" type="search" placeholder="Type to Search">
                            </b-form-input>

                            <b-input-group-append>
                                <b-button  :disabled="!filter" @click="filter = ''">Clear</b-button>
                            </b-input-group-append>
                        </b-input-group>
                    </b-form-group>
                </b-col>
                <!----- add category ----->
                <b-col sm="4">
                    <b-button class="w-100 " variant="primary" id="btn-addprd" @click="btnaddCtg">
                        <b-icon icon="plus" font-scale="2"></b-icon>Add category
                    </b-button>
                </b-col>

            </b-row>

            <!---------- MODAL ADD CATEGORY --------------->

            <b-modal id="modalAddcategory" ref="modalPostpond" title="Create your product">
                <form ref="form">
                    <div class="row">
                        <b-form-group class="col mb-4" id="input-name" v-model="category.name" label="Name:"
                            label-for="input-1">
                            <b-form-input v-model="category.name" id="input-1" type="text"
                                placeholder="Enter name product" required>
                            </b-form-input>
                        </b-form-group>

                        <b-form-group class="col-12 mb-4" id="input-quantity" label="Image:" label-for="input-4">
                            <b-form-file v-model="file2" class="mt-3" @change="onFileChange" plain></b-form-file>
                            <img :src="imageUpload" alt="" srcset="" class="w-50 mt-2">
                        </b-form-group>
                    </div>
                </form>
                <template #modal-footer>
                    <b-button variant="secondary" @click="hideModal">Cancel</b-button>
                    <b-button variant="primary" @click="handleModal">{{ textBtnModal }}</b-button>
                </template>
            </b-modal>

            <!----- TABLE PRODUCT ----->
            <b-table striped hover :filter="filter" :items="allcategories" :fields="fields" class="mt-2" sort-icon-left id="my-table">
                <!-- :per-page="perPage" :current-page="currentPage" :sort-by.sync="sortBy" :sort-desc.sync="sortDesc" -->

                <template #cell(Image)="dataimage">
                    <b-img :src="dataimage.value" alt="Responsive image" id="img-ctg"></b-img>
                </template>

                <template #cell(Checked)="dataSelected">
                    <b-form-checkbox id="checkbox-1" name="checkbox-1" value="accepted" unchecked-value="not_accepted"
                        @change="selectBox($event,dataSelected.item,dataSelected.index)">
                    </b-form-checkbox>
                </template>
                <template #cell(Action)="data">
                    <select class="form-select" aria-label="Select an action"
                        @change="handleActionChange($event,data.item)">
                        <option selected>Choose...</option>
                        <option value="edit">Edit</option>
                        <option value="delete">Delete</option>
                    </select>
                </template>
            </b-table>

            <b-pagination v-model="currentPage" :total-rows="rows" :per-page="perPage" aria-controls="my-table">
            </b-pagination>

        </b-container>

    </section>
</template>


<script>
    import axios from 'axios';
import { mapActions, mapState } from 'vuex';
    export default {
        name: "CategoryViews",
        data() {
            return {
                category: {
                    name: '',
                    image: ''
                },
                fields: [{
                        key: 'Checked',
                        sortable: false
                    },
                    {
                        key: 'Image',
                        sortable: false,
                        tdClass: 'image-cell'

                    },
                    {
                        key: 'Name',
                        sortable: true
                    },
                    {
                        key: 'Products',
                        sortable: false
                    },

                    {
                        key: 'Date',
                        sortable: true
                    },
                    {
                        key: 'Action',
                        sortable: false
                    },

                ],
                filter:null,
                imageShow: '',
                allcategories: '',
                imageUpload: '',
                textBtnModal: '',
                idCtgUpdDlt: '',
                teets: '',

            }
        },

        computed:{
            ...mapState('allProducts',{
                allProducts:state=>state.Products
            })
        },

        methods: {

            // MODAL FUNCTION
            hideModal() {
                this.$bvModal.hide('modalAddcategory')
            },

            btnaddCtg() {
                this.textBtnModal = 'Create'
                this.$bvModal.show('modalAddcategory')
            },

            handleModal() {
                if (this.textBtnModal === 'Update') {
                    console.log('work update')
                    this.editCtg(this.idCtgUpdDlt)
                    this.hideModal()
                }else if(this.textBtnModal === 'Create'){
                    this.addCategory()
                    this.hideModal()
                }
            },

            handleActionChange(ev, data) {
                this.idCtgUpdDlt = data.id
                if (ev.target.value === 'edit') {
                    // change content modal
                    this.textBtnModal = 'Update'
                    this.category.name=data.Name
                    this.imageUpload=data.Image
                    this.$bvModal.show('modalAddcategory')
                } else if (ev.target.value === 'delete') {
                    // delete category
                    const userConfirmed = window.confirm("Do you really want to delete this category ?");
                    if (userConfirmed) {
                        this.deletectg(this.idCtgUpdDlt)
                    }
                }
            },

            onFileChange(event) {
                const file = event.target.files[0]
                if (file) {
                    this.category.image = file
                    this.imageUpload = URL.createObjectURL(file)
                }
            },
            async addCategory() {
                try {
                    const token = localStorage.getItem('token')

                    const formData = new FormData()
                    formData.append('name', this.category.name)
                    formData.append('image', this.category.image)

                    const response = await axios.post('https://vercel-project-7hrz.vercel.app/api/category', formData, {
                        'Content-Type': 'multipart/form-data',
                        headers: {
                            Authorization: `Bearer ${token}`
                        }
                    })

                    console.log(response.data.message)
                } catch (error) {
                    console.log('error is :', error)
                }
            },

            async getCategories() {

                try {
                    const token = localStorage.getItem('token')
                    const response = await axios.get('https://vercel-project-7hrz.vercel.app/api/category', {
                        headers: {
                            Authorization: `Bearer ${token}`
                        }
                    })
                    this.allcategories = response.data.category.map(ele => {
                        const obj = new Object
                        obj.Image = ele.image
                        obj.Name = ele.name
                        obj.Products = this.allProducts.filter(prd=>prd.category.name===ele.name).length
                        obj.Date = ele.updatedAt.slice(0,10)
                        obj.id = ele._id
                        return obj
                    })
                } catch (error) {
                    console.log('error is :', error)
                }
            },

            async deletectg(id) {

                try {
                    const token = localStorage.getItem('token')
                    window.confirm()
                    const response = await axios.delete(`https://vercel-project-7hrz.vercel.app/api/category/${id}`, {
                        headers: {
                            Authorization: `Bearer ${token}`
                        }
                    })
                    console.log(response.data.message)
                } catch (error) {
                    console.log('error is :', error)
                }
            },

            async editCtg(id) {
                try {
                    const token = localStorage.getItem('token')
                    const formData = new FormData()
                    formData.append('name', this.category.name)
                    formData.append('image', this.category.image)
                    const response = await axios.put(`https://vercel-project-7hrz.vercel.app/api/category/${id}`, formData, {
                        'Content-Type': 'multipart/form-data',
                        headers: {
                            Authorization: `Bearer ${token}`
                        }
                    })
                    console.log(response.data.message)
                } catch (error) {
                    console.log('erreur is :', error)
                }
            },

            ...mapActions('allProducts',{
                fetchProducts:'ac_getProducts'
            }),

        },

        mounted() {
            this.getCategories()
            this.fetchProducts()
        }


    }
</script>

<style>
    #img-ctg {
        width: 100px;
        height: 100px
    }

    .image-cell {
        width: 141px;
    }
</style>