import axios from "axios"

const state={
    orders:[],
    conversionOrders:{New:[],Confirmed:[],Shipped:[],Delivered:[],Return:[],Cancelled:[]}
}

const mutations={
    
    m_getOrders(state,orders){
        state.orders=orders
    },
    
    m_GetConversionOrders(state,{type,orders}){
        state.conversionOrders[type]=orders
    },
    
}


const actions ={

   async ac_getOrders({commit}){
        try{
            const token=localStorage.getItem('token')
            const response=await axios.get('https://vercel-project-7hrz.vercel.app/api/orders',{
                headers:{
                    Authorization:`Bearer ${token}`
                }
            })
            const allOrders=response.data.orders
            commit('m_getOrders',allOrders)

        }
        catch(error){
            console.log(`error get all orders is ${error}`)
        }
    },

    async ac_GetConversionOrders({commit},type){
        try{
            const token=localStorage.getItem('token')
            const response = await axios.get('https://vercel-project-7hrz.vercel.app/api/orderConversionStatus', {
                headers: {
                    Authorization: `Bearer ${token}`
                }
            })
           const orders = response.data.conversionOrders.filter( ele=> ele.status === type ) 
           commit('m_GetConversionOrders',{type,orders})       

        }
        catch(error){
            console.log(`error get GetConversionOrders is ${error}`)
        }
    }

}
  
export default{
    namespaced: true,
    state,
    mutations,
    actions
}

