<template>
    <div class="stock">
        <b-container>
            <div class="row" id="stock-con">
                <h1 class="col text-center fw-bolder">Stock Products</h1>
                <div class="d-flex justify-content-between mt-5">
                    <!----- SEARCH INPUT ----->
                    <b-form-input id="search-input" type="search" placeholder="Search product " @input="searchprd">
                    </b-form-input>
                    <!----- FILTER INPUT ----->

                    <b-form-select size="sm" id="input-filter" :options="optionsFilter" v-model="selected">
                    </b-form-select>

                </div>
                <div class="mt-5" v-for="prd in ShowProductStock" :key="prd.id" id="stockProduct">
                    <div id="productStock" class="row d-flex align-items-center mb-4 ">
                        <div class="d-flex align-items-center gap-2 col-2" >
                            <img :src="prd.image" alt="" srcset="" id="img-prd">
                            <div id="title">
                                <h6 class="mb-1"> {{ prd.name }} </h6>
                                <p> <strong> {{ prd.quantity }}</strong></p>
                            </div>
                        </div>
                        <div id="title" class="col-9">
                            <b-progress :value="prd.quantity" :max="max" show-progress class="mb-3"
                                :variant=prd.variant>
                            </b-progress>
                        </div>
                        <div id="action" class="col-1">
                            <b-dropdown size="lg" variant="link" toggle-class="text-decoration-none" no-caret>
                                <template #button-content>
                                    <i class='bx bx-dots-horizontal-rounded'></i>
                                </template>
                                <b-dropdown-item href="#" @click="showModalAddStock(prd.id,prd.quantity)"> <span
                                        id="icon-action">+</span> Add to
                                    stock
                                </b-dropdown-item>
                                <b-dropdown-item href="#" @click="showModalSubsStock(prd.id,prd.quantity)">
                                    <span id="icon-action">-</span> Subtract from stock</b-dropdown-item>
                            </b-dropdown>
                        </div>
                    </div>
                    <hr>
                </div>

            </div>

            <!-- MODAL STOCK -->
            <b-modal id="modal-Stock" ref="modal" title="Add stock"  @ok="addOrsubQuantity"
                :ok-title="BtnModal">
                <form ref="form">
                    <b-form-group :label="textmodal" label-for="name-input" :invalid-feedback="msgInvalidInput"
                        :state="stateInputStock">
                        <b-form-input id="name-input" v-model="quantityModal" :state="stateInputStock" type="number"
                            required></b-form-input>
                    </b-form-group>
                </form>
            </b-modal>

        </b-container>
    </div>
</template>

<script>
    import axios from 'axios';
    import {
        mapActions,
        mapState
    } from 'vuex';
    export default {
        name: 'StockProduct',
        data() {
            return {
                max: 100,
                valuepo: 50,
                selected: 'All products',
                msgInvalidInput: '',
                optionsFilter: [{
                        value: 'All products',
                        text: 'All products'
                    },

                    {
                        value: 'Suffisant',
                        text: 'Suffisant'
                    },
                    {
                        value: 'Acceptable',
                        text: 'Acceptable'
                    },
                    {
                        value: 'Faible',
                        text: 'Faible'
                    },
                    {
                        value: 'Épuisé',
                        text: 'Épuisé'
                    },

                ],
                stateInputStock: null,
                categoryPrdStock: '',
                indexPrdStock: '',
                Send: 'Add Stock',
                ActionDropdown: '',
                ProductsFromDataBase: '',
                textmodal: '',
                BtnModal: '',
                productUpdatedQuantity: {
                    id: '',
                    quantity: ''
                },
                quantityModal: ''
            }
        },

        computed: {

            // STORE ALL PRODUCTS
            ...mapState('allProducts', {
                allProducts: state => state.Products
            }),

            createobjProducts() {
                let products = this.allProducts.map(ele => {
                    const obj = new Object
                    obj.name = ele.name
                    obj.image = ele.image
                    obj.id = ele._id
                    obj.quantity = ele.quantity
                    obj.variant = this.getProgressVariant(ele.quantity)
                    return obj
                })
                return products
            },

            ShowProductStock() {
                let result
                switch (this.selected) {
                    case 'Suffisant':
                        result = this.createobjProducts.filter(product => {
                            return Math.round(product.quantity) >= 75
                        })
                        break;

                    case 'Acceptable':
                        result = this.createobjProducts.filter(product => {
                            return Math.round(product.quantity) >= 45 && Math.round(product
                                .quantity) < 75
                        })
                        break;

                    case 'Faible':
                        result = this.createobjProducts.filter(product => {
                            return Math.round(product.quantity) >= 10 && Math.round(product
                                .quantity) < 45
                        })
                        break;

                    case 'Épuisé':
                        result = this.createobjProducts.filter(product => {
                            return Math.round(product.quantity) < 10
                        })
                        break;
                    default:
                        result = this.createobjProducts

                }
                return result
            }

        },

        methods: {

            getProgressVariant(valueProgres) {
                if (valueProgres >= 90) {
                    return 'success';
                } else if (valueProgres >= 70) {
                    return 'primary';
                } else if (valueProgres >= 50) {
                    return 'warning';
                } else {
                    return 'danger';
                }
            },

            searchprd(value) {
                let Products = Array.from(this.$el.querySelectorAll('#stockProduct'))
            
                Products.forEach(prd => {
                    let nameProduct= prd.children[0].children[0].lastChild.firstChild.textContent.toLowerCase()
                    prd.style.display = nameProduct.includes(value.toLowerCase()) ? '' : 'none'
                })
            },

            showModalAddStock(id, quantity) {
                this.$bvModal.show('modal-Stock')
                this.textmodal = 'Please add number quantity to your prdouct'
                this.BtnModal = "Add"
                this.productUpdatedQuantity.id = id
                this.productUpdatedQuantity.quantity = quantity
            },

            showModalSubsStock(id, quantity) {
                this.$bvModal.show('modal-Stock')
                this.textmodal = 'Please substract number quantity to your prdouct'
                this.BtnModal = 'Substract'
                this.productUpdatedQuantity.id = id
                this.productUpdatedQuantity.quantity = quantity
            },

            addOrsubQuantity() {
                if (this.BtnModal === "Add") {
                    this.productUpdatedQuantity.quantity = parseInt(this.productUpdatedQuantity.quantity) + parseInt(
                        this.quantityModal)
                    this.editQuantity(this.productUpdatedQuantity.quantity, this.productUpdatedQuantity.id)
                } else if (this.BtnModal === "Substract") {
                    this.productUpdatedQuantity.quantity = parseInt(this.productUpdatedQuantity.quantity) - parseInt(
                        this.quantityModal)
                    this.editQuantity(this.productUpdatedQuantity.quantity, this.productUpdatedQuantity.id)
                }
            },

            async getProducts() {
                try {
                    const token = localStorage.getItem('token')
                    const response = await axios.get('https://vercel-project-7hrz.vercel.app/api/product', {
                        headers: {
                            Authorization: `Bearer ${token}`
                        }
                    })
                    this.ProductsFromDataBase = response.data.products.map(ele => {
                        const obj = new Object
                        obj.name = ele.name
                        obj.id = ele._id
                        obj.quantity = ele.quantity
                        obj.variant = this.getProgressVariant(ele.quantity)
                        return obj
                    })

                } catch (error) {
                    console.log('error is :', error)
                }
            },

            async editQuantity(quantity, id) {
                try {
                    const token = localStorage.getItem('token')
                    const response = await axios.put(
                        `https://vercel-project-7hrz.vercel.app/api/productQuantity/${id}`, {
                            quantity: quantity
                        }, {
                            headers: {
                                Authorization: `Bearer ${token}`
                            }
                        })
                    console.log(response.data.message)
                } catch (error) {
                    console.log(`error the update is  : ${error}`)
                }
            },

            ...mapActions('allProducts', {
                fetchProducts: 'ac_getProducts'
            })
        },

        mounted() {
            this.fetchProducts()
        }


    }
</script>

<style scoped>
    .stock {
        padding-top: 23px;
        padding-bottom: 23px;
    }

    #stock-con {
        background: #14142d;
        margin-inline: 3px;
        border-radius: 24px;
        padding-inline: 20px;
        padding-top: 39px;
        padding-bottom: 39px;

    }

    #action {
        position: relative;
    }

    .dropdown {
        position: absolute;
        right: -6px;
        top: -29px;
    }

    #input-filter {
        width: 18%;
        height: 37px;
        background: var(--couleur-primaire-4);
        color: white;
        border: none;
        border-radius: 6px;
        width: 16%;
        height: 32px;
    }

    #search-input {
        width: 20%;
        height: 37px;
        background: transparent;
        border-color: var(--couleur-primaire-2);
    }

    #search-input::placeholder {
        color: var(--couleur-primaire-2);
    }

    #search-input:focus {
        color: white;
        box-shadow: none;
        border-color: var(--couleur-primaire-3);
    }

    hr {
        width: calc(100% - 24px);
        margin: auto;
        border: 1px solid #365cf5;
        border-radius: 26px;
    }

    .success {
        background-color: green;
    }

    .warning {
        background-color: yellow;
    }

    #icon-action {
        font-size: 20px;
        margin-right: 8px;
    }

    #img-prd{
        width:33% ;
    }

    p{
        margin: 0px;
        padding: 0px;
    }
</style>